import React, { useState } from 'react';
import './App.css';
import { getNumbers } from './utils';
import { Pagination } from './components/Pagination';

// eslint-disable-next-line @typescript-eslint/no-unused-vars
const items = getNumbers(1, 42)
  .map(n => `Item ${n}`);

export const App: React.FC = () => {
  const [allItems] = useState(items);
  const [itemsPerPage, setItemsPerPage] = useState(5);
  const [activePage, setActivePage] = useState(1);

  const pagesQuantity = Math.ceil(allItems.length / itemsPerPage);

  const handleChangePage = (page: number) => (
    page !== activePage
      ? setActivePage(page)
      : false
  );

  const handleNextPage = (page: number) => (
    page !== pagesQuantity ? setActivePage(page + 1) : false
  );

  const handlePrevPage = (page: number) => (
    page !== 1 ? setActivePage(page - 1) : false
  );

  const handleChangeItemsPerPage = (
    event: React.ChangeEvent<HTMLSelectElement>,
  ) => {
    setItemsPerPage(Number(event.target.value));
    setActivePage(1);
  };

  const fistItemInPage = activePage * itemsPerPage - itemsPerPage + 1;
  const lastItemInPage = activePage * itemsPerPage > allItems.length
    ? allItems.length
    : activePage * itemsPerPage;

  const itemsOnCurrentPage = () => {
    return getNumbers(fistItemInPage, lastItemInPage);
  };

  return (
    <div className="container">
      <h1>Items with Pagination</h1>

      <p className="lead" data-cy="info">
        {`Page ${activePage} (items ${fistItemInPage} - ${lastItemInPage} of ${allItems.length})`}
      </p>

      <div className="form-group row">
        <div className="col-3 col-sm-2 col-xl-1">
          <select
            data-cy="perPageSelector"
            id="perPageSelector"
            className="form-control"
            value={itemsPerPage}
            onChange={handleChangeItemsPerPage}
          >
            <option value="3">3</option>
            <option value="5">5</option>
            <option value="10">10</option>
            <option value="20">20</option>
          </select>
        </div>

        <label htmlFor="perPageSelector" className="col-form-label col">
          items per page
        </label>
      </div>

      <Pagination
        // allItems={allItems}
        // itemsPerPage={itemsPerPage}
        pagesQuantity={pagesQuantity}
        activePage={activePage}
        onPageChange={handleChangePage}
        onPrevPage={handlePrevPage}
        onNextPage={handleNextPage}
        itemsOnCurrentPage={itemsOnCurrentPage}
      />

    </div>
  );
};

export default App;
